.header {
  width: 100%;
  height: 3.5rem;
  background-color: #A5798C;
  box-shadow: 0 3px 3px #916276;
}

@media screen and (min-width: 230px) {
  .hamburger {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
    margin-top: 0.875rem;
    width: 2rem;
    height: 2rem;
  }
  .hamburger .bigMacOff {
    width: 100%;
    height: 2px;
    background-color: #F0FDF0;
    border-radius: 0.7rem;
    transition: border 1.2s;
  }
  .hamburger .bigMacOff::after, .hamburger .bigMacOff::before {
    position: absolute;
    content: "";
    height: 2px;
    background-color: #F0FDF0;
    border-radius: 0.7rem;
    transition: top 0.6s ease;
  }
  .hamburger .bigMacOff::after {
    left: 0;
    right: 0;
    top: 0.3rem;
    transform: rotate(0);
  }
  .hamburger .bigMacOff::before {
    left: 0;
    right: 0;
    bottom: 0.3rem;
    transform: rotate(0);
  }
  .hamburger .bigMacOn {
    width: 100%;
    border: 1px solid transparent;
  }
  .hamburger .bigMacOn::after, .hamburger .bigMacOn::before {
    position: absolute;
    content: "";
    left: 0;
    right: 0;
    border: 1px solid #F0FDF0;
    border-radius: 0.7rem;
    transition: transform 0.4s ease;
  }
  .hamburger .bigMacOn::after {
    top: 0.9rem;
    transform: rotate(45deg);
  }
  .hamburger .bigMacOn::before {
    bottom: 1rem;
    transform: rotate(-45deg);
  }
}
@media screen and (min-width: 760px) {
  .hamburger {
    display: none;
  }
}
@media screen and (min-width: 1366px) {
  .hamburger {
    display: none;
  }
}
@media screen and (min-width: 1920px) {
  .hamburger {
    display: none;
  }
}

@keyframes menuTurnOn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.menu {
  position: relative;
}
@media screen and (min-width: 230px) {
  .menu {
    display: none;
  }
}

.menuMobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3.5rem;
  padding: 2rem 2rem;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 50%;
  box-shadow: 0 1px 6px #888F73;
  animation-name: menuTurnOn;
  animation-duration: 0.4s;
  animation-timing-function: easy-in;
}
.menuMobile a {
  text-align: center;
  font-size: 1.5rem;
  color: #466079;
  padding: 1.5rem 0;
  width: 50%;
}
.menuMobile a:nth-child(1) {
  border-bottom: 1px solid #6889A8;
}
.menuMobile a:nth-child(2) {
  border-bottom: 1px solid #6889A8;
}
.menuMobile a:nth-child(3) {
  border-bottom: 1px solid #6889A8;
}
.menuMobile a:nth-child(4) {
  border-bottom: 1px solid #6889A8;
  margin-bottom: 1rem;
}
.menuMobile a:hover, .menuMobile a:active {
  color: #6889A8;
}

.logo {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 3.5rem;
}
@media screen and (max-width: 230px) {
  .logo {
    display: none;
  }
}
@media screen and (min-width: 230px) {
  .logo a {
    font-size: 1rem;
    font-weight: 400;
    color: #F0FDF0;
  }
}